import '@/styles/actions.scss';
import { ref, onMounted, defineComponent, watch } from 'vue';
import { faBoxOpen, faFileAudio, faAngleRight, faHashtag, faDatabase, faFileZipper, faCalendarPlus, faHouseLaptop, faCodeMerge } from '@fortawesome/free-solid-svg-icons';
import { library } from '@fortawesome/fontawesome-svg-core';
import { ElNotification } from 'element-plus';
library.add(faBoxOpen, faAngleRight, faFileAudio, faHashtag, faCodeMerge, faDatabase, faFileZipper, faCalendarPlus, faHouseLaptop);
import { vLoading } from 'element-plus/es/components/loading/src/directive';
import 'element-plus/theme-chalk/el-loading.css';
import 'element-plus/theme-chalk/el-notification.css';
import { urls } from './urls';
import { useRoute } from 'vue-router';
export default defineComponent({
  name: 'InstallerIndex',
  directives: {
    elloading: vLoading
  },

  setup() {
    const loading = ref(true); // eslint-disable-next-line @typescript-eslint/no-explicit-any

    const result = ref({});
    const route = useRoute(); // eslint-disable-next-line @typescript-eslint/no-explicit-any

    const cachedResult = {};

    const load = async () => {
      const url = await urls[(route.query.type || 'pc-cn').toString()].url;

      if (cachedResult[url]) {
        result.value = cachedResult[url];
        return;
      }

      loading.value = true;
      return fetch(url).then(res => {
        return res.json();
      }).then(res => {
        result.value = res.data;
        loading.value = false;
        cachedResult[url] = res.data;
      }).catch(err => {
        ElNotification.error({
          title: '出错了',
          message: err.message
        });
      });
    };

    onMounted(load);
    watch(route, load);

    const basename = path => {
      const pathArr = path.split('/');
      return pathArr[pathArr.length - 1];
    };

    const formatSize = size => {
      if (size < 1024) {
        return `${size}B`;
      } else if (size < 1024 * 1024) {
        return `${(size / 1024).toFixed(2)}KB`;
      } else if (size < 1024 * 1024 * 1024) {
        return `${(size / 1024 / 1024).toFixed(2)}MB`;
      } else {
        return `${(size / 1024 / 1024 / 1024).toFixed(2)}GB`;
      }
    };

    return {
      loading,
      result,
      basename,
      formatSize,
      urls
    };
  }

});